#top {
  height: 1px;
}

#headcontainer {
  /* background-image: radial-gradient(circle, black, darkblue, blue);
  background-position: center;
  background-size: cover;
  background-repeat: repeat; */
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 600px;
  justify-content: space-evenly;
  align-items: center;
}

#headline {
  margin: 20px auto 0;
  padding: 10px 10%;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 255, 0),
    rgba(0, 255, 0, 1),
    rgba(0, 0, 255, 0)
  );
  color: blue;
  font-weight: 700;
  font-size: 25px;
 }

@media only screen and (max-width: 849px) {
  .headline {
    margin: 12% 0 0;
  }
}

.loading {
  margin-top: 250px;
}

.crazy1 {
  animation: rotate 2s ;
}

.crazy2 {
  animation: flash 2s ;
 }

@keyframes rotate {
  10% {transform: rotate(360deg);}
  30% {transform: rotate(-360deg);}
  60% {transform: scale(2)}
  80% {transform: scale(0.1)}
  100% {transform: scale(1)}
}

@keyframes flash {
  30% {filter: hue-rotate(120deg);}
  60% {filter: hue-rotate(240deg);}
  90% {filter: hue-rotate(360deg);}
}