@import url("https://fonts.googleapis.com/css2?family=Anton&family=Francois+One&family=Roboto:ital,wght@0,100;0,400;0,700;1,900&display=swap");

.App {
  text-align: center;
  width: 100%;
}
body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

html {
  background-image: radial-gradient(circle, black, darkblue, blue);
  height: 100vh;
}

.topnav {
  position: fixed;
  display: flex;
  flex-wrap: wrap-reverse;
  background-color: #333;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}

.topnav div {
  float: left;
  color: #f2f2f2;
  text-align: center;
  text-decoration: none;
  font-size: 20px;
  padding-top: 14px;
  padding-bottom: 14px;
}

.topnav button {
  background-color: blue;
  color: red;
  transform: scale(1.3);
  outline: none;
  border: 4px double lightblue;
  margin: 8px 20px;
  transition: 0.5s;
}

.topnav button:hover {
  background-color: red;
  color: blue;
  transform: scale(1.5);
  outline: none;
}

#magnify {
  margin-top: 8px;
  cursor: pointer;
}

.topnav button:active {
  background-color: red;
  color: red;
  transform: scale(1.1);
  outline: none;
}

.topnav input {
  background-color: blue;
  color: #87ff09;
  line-height: 1.8em;
  border-radius: 10px;
  padding: 2px;
  margin: 5px 10px;
}

.topnav input::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #87ff09;
}

.home-title {
  position: relative;
  left: 50%;
  transform: translateX(-140%);
}

#title {
  background-image: linear-gradient(90deg, #87ff09, lightblue, red);
  color: transparent;
  background-clip: text;
  font-size: 1.8em;
  font-weight: 900;
}

.landing-title {
  margin: 10px auto;
  text-align: center;
  top: 10px;
  width: 443px;
}

#select-language {
  position: relative;
  transform: translateY(-10%);
  margin-left: 10px;
  padding: 5px 10px;
  outline: none;
  border-radius: 5px;
  border: 1px solid white;
  background-color: blue;
  color: white;
  cursor: pointer;
}

#cookieBtn {
  padding: 30% 40%;
  margin-left: -40%;
}

@media only screen and (max-width: 1242px) {
  #title {
    left: 0%;
    transform: translateX(0%);
  }
}

@media only screen and (max-width: 849px) {
  .topnav {
    justify-content: center;
  }
}

#land-load {
  margin-top: 40vh;
}

/**finger spinner***/
.fingerprint-spinner,
.fingerprint-spinner * {
  box-sizing: border-box;
}

.fingerprint-spinner {
  /* 
  transform: scale(1.2, 1.2); */
  max-width: 100vw; /* 
  overflow: hidden; */
  position: relative;
}

.fingerprint-spinner .spinner-ring {
  position: absolute;
  border-radius: 50%;
  border: 2px solid transparent;
  border-top-color: transparent;
  animation: fingerprint-spinner-animation 1500ms
    cubic-bezier(0.68, -0.75, 0.265, 1.75) infinite forwards;
  margin: auto;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  box-shadow: 3px 3px 5px lightblue, -3px 3px 5px #87ff09;
}

.fingerprint-spinner .spinner-ring:nth-child(1) {
  height: calc(150px / 9 + 0 * 150px / 9);
  width: calc(150px / 9 + 0 * 150px / 9);
  animation-delay: calc(50ms * 1);
}

.fingerprint-spinner .spinner-ring:nth-child(2) {
  height: calc(150px / 9 + 1 * 150px / 9);
  width: calc(150px / 9 + 1 * 150px / 9);
  animation-delay: calc(50ms * 2);
}

.fingerprint-spinner .spinner-ring:nth-child(3) {
  height: calc(150px / 9 + 2 * 150px / 9);
  width: calc(150px / 9 + 2 * 150px / 9);
  animation-delay: calc(50ms * 3);
}

.fingerprint-spinner .spinner-ring:nth-child(4) {
  height: calc(150px / 9 + 3 * 150px / 9);
  width: calc(150px / 9 + 3 * 150px / 9);
  animation-delay: calc(50ms * 4);
}

.fingerprint-spinner .spinner-ring:nth-child(5) {
  height: calc(150px / 9 + 4 * 150px / 9);
  width: calc(150px / 9 + 4 * 150px / 9);
  animation-delay: calc(50ms * 5);
}

.fingerprint-spinner .spinner-ring:nth-child(6) {
  height: calc(150px / 9 + 5 * 150px / 9);
  width: calc(150px / 9 + 5 * 150px / 9);
  animation-delay: calc(50ms * 6);
}

.fingerprint-spinner .spinner-ring:nth-child(7) {
  height: calc(150px / 9 + 6 * 150px / 9);
  width: calc(150px / 9 + 6 * 150px / 9);
  animation-delay: calc(50ms * 7);
}

.fingerprint-spinner .spinner-ring:nth-child(8) {
  height: calc(150px / 9 + 7 * 150px / 9);
  width: calc(150px / 9 + 7 * 150px / 9);
  animation-delay: calc(50ms * 8);
}

.fingerprint-spinner .spinner-ring:nth-child(9) {
  height: calc(150px / 9 + 8 * 150px / 9);
  width: calc(150px / 9 + 8 * 150px / 9);
  animation-delay: calc(50ms * 9);
}

@keyframes fingerprint-spinner-animation {
  100% {
    transform: rotate(360deg);
  }
}

.articleTitle {
  grid-area: articleTitle;
}
.articlePicture {
  grid-area: articlePicture;
}
.articleContent {
  grid-area: articleContent;
}
.articleDate {
  grid-area: articleDate;
}
.url {
  grid-area: articleUrl;
}

.article {
  margin: 30px;
  border-radius: 3px;
  display: grid;
  grid-gap: 20px;
  background-color: bisque;
  padding: 20px;
  font-family: "Roboto", sans-serif;
  box-shadow: black 15px 10px 25px 0px;
  width: 70%;
  color: rgb(73, 71, 71);
  text-align: justify;
  position: relative;
  left: 50%;
  transform: translateX(-55%);
}

.article:nth-of-type(even) {
  grid-template-areas:
    "articlePicture articleTitle articleTitle articleTitle"
    "articlePicture articleContent articleContent articleContent"
    "articlePicture articleContent articleContent articleContent"
    "articleDate articleUrl articleUrl articleUrl";
}

.article:nth-of-type(odd) {
  grid-template-areas:
    "articleTitle articleTitle articleTitle articlePicture"
    "articleContent articleContent articleContent articlePicture"
    "articleContent articleContent articleContent articlePicture"
    "articleUrl articleUrl articleUrl articleDate";
}

.articleTitle {
  font-family: "Francois One", sans-serif;
  text-align: center;
}

.articlePicture {
  width: 100%;
  background-image: linear-gradient(
    45deg,
    blue,
    red,
    black,
    yellow,
    lime,
    blue
  );
  padding: 5px;
  border-radius: 10px;
}

.articleContent {
  text-align: justify;
  padding: 20px 0;
  font-size: 1.2em;
}

.articleDate {
  text-align: right;
}

.url {
  text-decoration: none;
  margin: 10px;
  text-align: center;
  font-size: 1em;
  display: inline-block;
  position: relative;
}

.trick {
  bottom: 50px;
}

#egg {
  height: 50px;
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 99;
  opacity: 0;
  transition: 2s;
}

#egg:hover {
  opacity: 1;
}
