.crazy1 {
  -webkit-animation: rotate 2s ;
          animation: rotate 2s ;
}

.crazy2 {
  -webkit-animation: flash 2s ;
          animation: flash 2s ;
 }

@-webkit-keyframes rotate {
  10% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
  30% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
  60% {-webkit-transform: scale(2);transform: scale(2)}
  80% {-webkit-transform: scale(0.1);transform: scale(0.1)}
  100% {-webkit-transform: scale(1);transform: scale(1)}
}

@keyframes rotate {
  10% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
  30% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
  60% {-webkit-transform: scale(2);transform: scale(2)}
  80% {-webkit-transform: scale(0.1);transform: scale(0.1)}
  100% {-webkit-transform: scale(1);transform: scale(1)}
}

@-webkit-keyframes flash {
  30% {-webkit-filter: hue-rotate(120deg);filter: hue-rotate(120deg);}
  60% {-webkit-filter: hue-rotate(240deg);filter: hue-rotate(240deg);}
  90% {-webkit-filter: hue-rotate(360deg);filter: hue-rotate(360deg);}
}

@keyframes flash {
  30% {-webkit-filter: hue-rotate(120deg);filter: hue-rotate(120deg);}
  60% {-webkit-filter: hue-rotate(240deg);filter: hue-rotate(240deg);}
  90% {-webkit-filter: hue-rotate(360deg);filter: hue-rotate(360deg);}
}
